import React, { useState } from "react";
import NavMenu from "./NavMenu/NavMenu";
import { NavLink } from "react-router-dom";
import styles from "./Header.module.scss";

const Header = (props) => {
  const links = [
    { type: "normal", text: "Features", url: "/" },
    { type: "normal", text: "Pricing", url: "/" },
    { type: "normal", text: "Resources", url: "/" },
    { type: "option", text: "Login", url: "/" },
    { type: "option", text: "Sign Up", url: "/" },
  ];
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <div className={styles.padding__container}>
      <header className={styles.header__container}>
        <NavLink to="/">
          <h1>Shortly</h1>
        </NavLink>
        <NavMenu
          className={styles.menuItems}
          links={links}
          menuOpen={menuOpen}
        ></NavMenu>
        <div
          className={[
            styles.menuIcon,
            menuOpen ? styles.menuIconOpen : "",
          ].join(" ")}
          onClick={() => setMenuOpen(!menuOpen)}
        >
          <span></span>
          <span></span>
          <span></span>
        </div>
      </header>
    </div>
  );
};

export default Header;
